import HeroSlider, { Overlay, Slide, MenuNav } from "hero-slider";
import Wrapper from "../componentes/Wrapper";
import Title from "../componentes/Title";
import Subtitle from '../componentes/Subtitle';

const bogliasco = "https://www.questionpro.com/blog/wp-content/uploads/2023/04/Thumb-blog-BR-and-PT-42-750x420.png";
const countyClare = "https://content.news.ifood.com.br/uploads/2023/01/capa_aigenerative.jpg";
const craterRock = "data:image/jpeg;base64,";
const giauPass = "https://www.otempo.com.br/image/contentid/policy:1.2878955:1687996461/inteligencia-artificial-png.png?f=3x2&w=1224";

export default function BasicSlider() {
  return (
    <HeroSlider
      height={"100vh"}
      autoplay
      controller={{
        initialSlide: 1,
        slidingDuration: 500,
        slidingDelay: 100,
        onSliding: (nextSlide) =>
          console.debug("onSliding(nextSlide): ", nextSlide),
        onBeforeSliding: (previousSlide, nextSlide) =>
          console.debug(
            "onBeforeSliding(previousSlide, nextSlide): ",
            previousSlide,
            nextSlide
          ),
        onAfterSliding: (nextSlide) =>
          console.debug("onAfterSliding(nextSlide): ", nextSlide)
      }}
    >
      <Overlay>
        <Wrapper>
          <Title>Criao de sites</Title>
          <Subtitle>
          Seu site  o seu carto de visitas online, por isso ele deve refletir a sua identidade e os seus valores. Ns somos especialistas em criar sites personalizados, exclusivos e que se destacam da concorrncia..
          </Subtitle>
        </Wrapper>
      </Overlay>

      <Slide
        shouldRenderMask
        label="RJ - Brasil"
        background={{
          backgroundImageSrc: giauPass
        }}
      />

      <Slide
        shouldRenderMask
        label="New york - United States"
        background={{
          backgroundImageSrc: bogliasco
        }}
      />

      <Slide
        shouldRenderMask
        label="Londres - Inglaterra"
        background={{
          backgroundImageSrc: countyClare
        }}
      />

      <Slide
        shouldRenderMask
        label="Qunia - Africa"
        background={{
          backgroundImageSrc: craterRock
        }}
      />

      <MenuNav />
    </HeroSlider>
  );
}
