import React from 'react';
import { useState } from 'react';
import emailjs from '@emailjs/browser'; //necessrio instalar emailjs

function Contatos() {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [message, setMessage] = useState('');

    function sendEmail(e) {
        e.preventDefault();

        if (name === '' || email === '' || message === '') {
            alert("Preencha todos os campos");           
            return;
        }

        const templateParams = {
            from_name: name,
            email: email,
            message: message
        }

        emailjs.send("service_fhdtqca", "template_1enbt2d", templateParams, "ikOnbZRr6I4sF_uS2")
            .then((response) => {
                console.log("Email enviado", response.status, response.text)
                setName('')
                setEmail('')
                setMessage('')

                alert("Enviado, agradecemos o contato.")

            }, (err) => {
                console.log("ERRO:", err)
            })
    }

    return (
        <div className="container-fluid bg-grey">
            <h2 className="text-center">CONTATO</h2>

            <form className='form' onSubmit={sendEmail}>
                <input
                    className="col-sm-7"
                    type="text"
                    placeholder="Name"
                    onChange={(e) => setName(e.target.value)}
                    value={name}
                />

                <input
                    className="col-sm-6 form-group"
                    type="text"
                    placeholder="Email"
                    onChange={(e) => setEmail(e.target.value)}
                    value={email}
                />

                <textarea
                    className="form-control" id="comments" name="comments"
                    placeholder="Comment"
                    onChange={(e) => setMessage(e.target.value)}
                    value={message}
                />

                <input className="button" type="submit" value="Enviar" />
            </form>

        </div >
    );
}
export default Contatos;